import React, { useState, useEffect, useRef, useContext } from "react";
import { Wrap, ButtonWrap } from "./styled";
import { Modal, Table, Input } from 'antd';
import { Button } from "../../components/Button";
import './index.scss';
import { Course } from "./course";
import { IItem, useStudents } from "./action";
import LoadingPage from "../../components/loading";
import { modifyStudents } from "../../servers/students";



export default () => {

  const { loading, setLoading, data, setData } = useStudents();

  const [visible, setVisible] = useState(false);

  const handleChangeValue = (key: keyof IItem, value: any, item: IItem) => {
    const newData = data.map(v => {
      if (v.id === item.id) {
        return { ...v, [key]: value };
      }
      return v;
    });
    setData(newData);
  }

  const columns = [
    {
      title: '用户ID',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: '用户名',
      dataIndex: 'username',
      key: 'username',
      render: (value: string, item: IItem) => {
        if (item.isModify) {
          return <Input
            className='auto-table-input'
            placeholder='请填写'
            value={value}
            onChange={(e) => { handleChangeValue('username', e.target.value, item) }}
          />
        } else {
          return value;
        }
      }
    },
    {
      title: '密码',
      dataIndex: 'password',
      key: 'password',
      render: (value: string, item: IItem) => {
        if (item.isModify) {
          return <Input
            className='auto-table-input'
            placeholder='请填写'
            value={value}
            onChange={(e) => { handleChangeValue('password', e.target.value, item) }}
          />
        } else {
          return value?"***":'';
        }
      }
    },
    {
      title: '手机号',
      dataIndex: 'phone_number',
      key: 'phone_number',
      render: (value: string, item: IItem) => {
        if (item.isModify) {
          return <Input
            className='auto-table-input'
            placeholder='请填写'
            value={value}
            onChange={(e) => { handleChangeValue('phone_number', e.target.value, item) }}
          />
        } else {
          return value;
        }
      }
    },
    {
      title: '加速器端口',
      dataIndex: 'web_accelerator_name',
      key: 'web_accelerator_name',
      render: (value: string, item: IItem) => {
        if (item.isModify) {
          return <Input
            className='auto-table-input'
            placeholder='请填写'
            value={value}
            onChange={(e) => { handleChangeValue('web_accelerator_name', e.target.value, item) }}
          />
        } else {
          return value;
        }
      }
    },
    {
      title: '加速器链接',
      dataIndex: 'web_accelerator_link',
      key: 'web_accelerator_link',
      render: (value: string, item: IItem) => {
        if (item.isModify) {
          return <Input
            className='auto-table-input'
            placeholder='请填写'
            value={value}
            onChange={(e) => { handleChangeValue('web_accelerator_link', e.target.value, item) }}
          />
        } else {
          return value;
        }
      }
    },
    {
      title: '购买课程数量',
      dataIndex: 'name2',
      key: 'name2',
    },
    {
      title: '操作',
      key: 'operate',
      render: (item: IItem) => {
        if (item.isModify) {
          return <>
            <Button
              text='确定'
              style={{ marginRight: 10 }}
              onPress={async () => {
                const res = await modifyStudents(item);
                return;
                const newData = data.map(v => v.id === item.id ? { ...v, isModify: false } : v);
                setData(newData);
              }}
            />
            <Button
              text='取消'
              onPress={async () => {
                const newData = data.filter(v => v.id !== item.id);
                if (item.id === 0) {
                  setData(newData);
                } else {
                  const newData = data.map(v => v.id === item.id ? { ...v, isModify: false } : v);
                  setData(newData);
                }

              }}
            />
          </>
        }
        return <>
          <Button
            text='修改'
            style={{ marginRight: 10 }}
            onPress={async () => {
              const newData = data.map(v => v.id === item.id ? { ...v, isModify: true } : v);
              setData(newData);
            }}
          />
          <Button
            text='删除'
            onPress={async () => {
              const newData = data.filter(v => v.id !== item.id);
              setData(newData);
            }}
          />
        </>
      }
    },
    {
      title: '课程详情',
      key: 'name5',
      render: () =>
        <Button
          text='查看'
          onPress={getDetail}
        />
    },
  ];

  const getDetail = async () => {
    setVisible(true);
  }

  return (
    <Wrap>
      <ButtonWrap>
        <Button onPress={async () => {
          setData(v => [{
            id: 0,
            username: "",
            phone_number: '',
            web_accelerator_name: '',
            web_accelerator_link: '',
            password: '',
            isModify: true
          }, ...v,])
        }}
          text='新增用户'
        />
      </ButtonWrap>

      <Table
        pagination={{
          hideOnSinglePage: true
        }}
        rowKey={(item) => item.id}
        dataSource={data}
        columns={columns.map(v => ({ ...v, align: 'center' }))}
      />
      <Modal
        style={{ width: 1200 }}
        title=""
        visible={visible}
        onCancel={() => setVisible(false)}
        okText='确定'
        cancelText='取消'
        wrapClassName={'detail-modal'}
      >
        <Course />
      </Modal>
      <LoadingPage isLoading={loading} />
    </Wrap>
  )
};
