import { useEffect, useState } from "react"
import { fetchStudents } from "../../servers/students";


export interface IItem {
    id: number,
    username: string,
    password:string,
    phone_number: string,
    web_accelerator_name: string,
    web_accelerator_link: string,
    isModify: boolean,
}

export const useStudents = () => {
    const [data, setData] = useState<IItem[]>([]);
    const [loading, setLoading] = useState(true);
    useEffect(() => {
        fetchStudents().then((v) => {
            const newData = v?.data_list?.map(v => (
                {
                    ...v,
                    isModify: false,
                }
            ))
            setData(newData);
        }).finally(() => {
            setLoading(false);
        })
    }, []);

    return {
        loading,
        data,
        setLoading,
        setData
    }
}

