import { get, post } from "../utils/rpc"

export interface IRes {
    errorCode: string,
    executed: boolean,
    message: string,
    success: boolean
}

interface Istudent {
    id: number
    username: string
    password:string
    phone_number: string
    web_accelerator_link: string
    web_accelerator_name: string
}
export interface fetchStudentsRes extends IRes {
    data_list: Istudent[]
}

export const fetchStudents = (): Promise<fetchStudentsRes> => {
    return get({ url: '/students' });
}

export interface modifyStudentssRes extends IRes {

}

export const modifyStudents = (param: Istudent): Promise<modifyStudentssRes> => {
    return post({ url: '/students', body: { ...param,name:'testname' } });
}