import { Input, Table } from "antd";
import React, { useState, useEffect, useRef } from "react";
import { Button } from "../../components/Button";
import { CustomSelect } from "../../components/CustomSelect";
import { ButtonWrap, Wrap } from './styled';

interface IItem {
    key: number,
    name: string,
    otherName: string,
    phone: string,
    name1: string,
    name2: string,
    name3: string,
    isModify: boolean,
}


interface ICourse {

}


export const Course: React.FC<ICourse> = ({ }) => {

    const [data, setData] = useState<IItem[]>([{
        key: 0,
        name: '张三',
        otherName: "2",
        phone: '13801380000',
        name1: 'QYOC001',
        name2: '2',
        name3: '2',
        isModify: false
    }])

    const handleChangeValue = (key: keyof IItem, value: any, item: IItem) => {
        const newData = data.map(v => {
            if (v.key === item.key) {
                return { ...v, [key]: value };
            }
            return v;
        });
        setData(newData);
    }

    const columns = [
        {
            title: '课程编号',
            dataIndex: 'name',
            key: 'name',
            width:150,
            render: (value: string, item: IItem) => {
                if (item.isModify) {
                    return <Input
                        className='auto-table-input'
                        placeholder='请填写'
                        value={value}
                        onChange={(e) => { handleChangeValue('name', e.target.value, item) }}
                    />
                } else {
                    return value;
                }
            }
        },
        {
            title: '课程名称',
            dataIndex: 'otherName',
            key: 'otherName',
            width:150,
            render: (value: string, item: IItem) => {
                if (item.isModify) {
                    return <CustomSelect
                        initValue={value}
                        selectData={[
                            { key: '1', value: '课程1' },
                            { key: '2', value: '课程2' },
                            { key: '3', value: '课程3' },
                        ]}
                        onChange={(key) => {
                            handleChangeValue('otherName', key,item)
                        }}
                    />
                } else {
                    return [
                        { key: '1', value: '课程1' },
                        { key: '2', value: '课程2' },
                        { key: '3', value: '课程3' },
                    ].find(v=>v.key===value)?.value;
                }
            }
        },
        {
            title: '账号',
            dataIndex: 'phone',
            key: 'phone',
            width:150,
            render: (value: string, item: IItem) => {
                if (item.isModify) {
                    return <Input
                        className='auto-table-input'
                        placeholder='请填写'
                        value={value}
                        onChange={(e) => { handleChangeValue('phone', e.target.value, item) }}
                    />
                } else {
                    return value;
                }
            }
        },
        {
            title: '密码',
            dataIndex: 'name1',
            key: 'name1',
            width:150,
            render: (value: string, item: IItem) => {
                if (item.isModify) {
                    return <Input
                        className='auto-table-input'
                        placeholder='请填写'
                        value={value}
                        onChange={(e) => { handleChangeValue('name1', e.target.value, item) }}
                    />
                } else {
                    return value;
                }
            }
        },
      
        {
            title: '失效日期',
            dataIndex: 'name2',
            key: 'name2',
            width:100,
        },
        {
            title: '操作',
            key: 'name5',
            width:100,
            render: (item: IItem) => {
                if (item.isModify) {
                    return <>
                        <Button
                            text='确定'
                            style={{ marginRight: 10 }}
                            onPress={async () => {
                                const newData = data.map(v => v.key === item.key ? { ...v, isModify: false } : v);
                                setData(newData);
                            }}
                        />
                        <Button
                            text='取消'
                            onPress={async () => {
                                const newData = data.filter(v => v.key !== item.key);
                                setData(newData);
                            }}
                        />
                    </>
                }
                return <>
                    <Button
                        style={{ marginRight: 10 }}
                        text='修改'
                        onPress={async () => {
                            const newData = data.map(v => v.key === item.key ? { ...v, isModify: true } : v);
                            setData(newData);
                        }}
                    />
                    <Button
                        text='删除'
                        onPress={async () => {
                            const newData = data.filter(v => v.key !== item.key);
                            setData(newData);
                        }}
                    />
                </>
            }
        },
    ];
    return (
        <div style={{paddingTop:'30px'}}>
            <ButtonWrap>
                <Button onPress={async () => {
                    setData(v => [...v, {
                        key: data.length,
                        name: '',
                        otherName: "",
                        phone: '',
                        name1: '',
                        name2: '',
                        name3: "",
                        isModify: true
                    }])
                }}
                    text='新增学生课程'
                />
            </ButtonWrap>
            <Table dataSource={data} columns={columns.map(v => ({ ...v, align: 'center' }))} />
        </div>
    );
};
